import React from 'react';
import HomeNavbar from '../../components/navbar/homeNavbar';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Table from 'react-bootstrap/Table';


class DownloadCenter extends React.Component {
    render() {
        return (
            <div>
                <HomeNavbar
                    islogged={this.props.islogged}
                    user={this.props.user}
                    clearSession={this.props.clearSession}
                />
                <Container fluid className="mt-3">
                    <Row noGutters className="align-items-center justify-content-center">
                        <Col md={4}>
                            <h2>
                                Centro de descargas
                            </h2>
                        </Col>
                    </Row>
                    <Row className="ml-5 mr-5">
                        <Table striped bordered hover size="sm">
                            <thead>
                                <tr>
                                    <th>Tipo</th>
                                    <th>Plataforma</th>
                                    <th>Nombre del archivo</th>
                                    <th>Checksum MD5</th>
                                    <th>Checksum SHA256</th>
                                    <th>Descargar</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td rowSpan="3">A<br/>P<br/>I</td>
                                    <td>Windows 64 bits</td>
                                    <td>wstt-MSVC2017-64bit.zip</td>
                                    <td>0ac5b0b8adb58be1daca7a21fc0180f5</td>
                                    <td>6bf5e4150d886f788f414505731d100b4487fb047df5070fbfdd4c0af5960904</td>
                                    <td><a className="btn btn-secondary" href="/libraries/wstt-MSVC2017-64bit.zip" download="wstt-MSVC2017-64bit.zip">Descargar</a></td>
                                </tr>
                                <tr>
                                    <td>wstt-rpi-gcc.zip</td>
                                    <td>Raspbian</td>
                                    <td>d6cc717546fee497f7aa325b35c14db1</td>
                                    <td>a701a60bc158f17e043c6d77eb6a6abee825583c85253fb5dd3be64cef7a58fe</td>
                                    <td><a className="btn btn-secondary" href="/libraries/wstt-rpi-gcc.zip" download="wstt-rpi-gcc.zip">Descargar</a></td>
                                </tr>
                                <tr>
                                    <td>wstt-WASM.zip</td>
                                    <td>WebAssembly</td>
                                    <td>aa3574efe835ea08540987e0b72252cc</td>
                                    <td>75bb583502564e6753131a2c48d7ae1e107b3a5819f0e78285b4539da516f0a6</td>
                                    <td><a className="btn btn-secondary" href="/libraries/wstt-WASM.zip" download="wstt-WASM.zip">Descargar</a></td>
                                </tr>
                            </tbody>
                        </Table>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default DownloadCenter;


